<template>
  <div id="forget-password">

    <!-- logo start -->
    <div class="logo">
      <img src="./images/logo.png" alt="">
    </div>
    <!-- logo end -->

    <!-- shop select start -->
    <div class="select-content">
      <div class="label">
        注册
      </div>

      <div class="from-content">
        <el-form class="password-form" :model="passwordForm" label-width="87px">

          <div>
            <el-form-item label="姓名">
              <el-input :class="{'errtip': isShownNameErr }"
              v-model="passwordForm.name"
              placeholder="请输入姓名"
              @change="clearerrTip">
              </el-input>
            </el-form-item>
            <span v-if="isShownNameErr" class="errMessage">请输入姓名</span>
          </div>
          
          <div>
            <el-form-item label="性别">
                <el-radio-group v-model="passwordForm.sex" @change="clearerrTip">
                  <el-radio 
                  v-for="item in sexOptions" 
                  :label="item.value"
                  :key="item.value"
                  
                  >{{item.label}}</el-radio>
                </el-radio-group>
            </el-form-item>
          </div>

          <div class="check-phone">
            <el-form-item label="登录手机号">
              <el-input :class="{'errtip': isShowPhoneErr || isShowRepeatErr ||isShowPhoneEmptyErr}" 
              v-model="passwordForm.loginPhone" 
              placeholder="输入登录手机号"
              @keyup.native="checkPhoneNum"
              @change="clearerrTip"
              ></el-input>              
            </el-form-item>
            <span v-if="isShowPhoneEmptyErr" class="errMessage">手机号不能为空</span>
            <span v-if="isShowPhoneErr" class="errMessage">手机号码输入错误</span>
            <span v-if="isShowRepeatErr" class="errMessage">手机号已被使用，请更换</span>
          </div>

          
          <div class="check-code">
            <el-form-item label="图形验证码">
              <el-input :class="{'errtip': isShowcodeErr}" 
              v-model="passwordForm.code"
              placeholder="输入图形验证码"
              @change="clearerrTip"></el-input>
            </el-form-item>
            <div class="check-img" @click="changeCheckImg">
                <img :src="codeimgurl" alt="验证图片" />
            </div>
            <span v-if="isShowcodeErr" class="errMessage">图形验证码输入错误</span>
          </div>

          <div class="check-msg">
            <el-form-item label="短信验证码">
              <el-input :class="{'errtip': isShowPhonecodeErr || isShowPhonecodeEmptyErr}" 
              v-model="passwordForm.messagecode"
              placeholder="输入短信验证码"
              @change="clearerrTip"></el-input>
            </el-form-item>
            <div v-if="!isShowCheckGrey" class="check-message gray">{{checkMsgGray}}</div>
            <div v-else class="check-message" @click="sendPhoneNum">{{ checkMsg }}</div>
            <span v-if="isShowPhonecodeEmptyErr" class="errMessage">请输入短信验证码</span>
            <span v-if="isShowPhonecodeErr" class="errMessage">短信验证码输入错误</span>
          </div>

          <div>
            <el-form-item label="密码">
              <el-input :class="{'errtip': isShowPasswordErr || isShowpasswordEmptyErr}"
              v-model="passwordForm.newPassword"
              placeholder="输入密码"
              :type="pwdType"
              @change="clearerrTip"
              @keypress.native="detectCapsLock"
              ></el-input>
            </el-form-item>
            <span v-if="isShowCapti" class="errMessage">大写锁定已经开启</span>
          </div>

          <div>
            <el-form-item label="确认密码">
              <el-input :class="{'errtip': isShowPasswordErr || isShowpasswordEmptyErr}"
              v-model="passwordForm.newPasswordConfrim"
              placeholder="重复密码"
              :type="pwdType"
              @blur="checkPassword"
              @change="clearerrTip"
              @keypress.native="detectCapsLock"
              ></el-input>
            </el-form-item>
             <span v-if="isShowpasswordEmptyErr" class="errMessage">请输入密码</span>
             <span v-if="isShowPasswordErr" class="errMessage">两次密码输入不一致</span>
          </div>

          <div>
            <el-form-item>
              <el-checkbox v-model="passwordForm.protocol">
                我接受并同意用户协议
              </el-checkbox>
              <span>《xxx》</span>
            </el-form-item>
          </div>
        </el-form>
      </div>

        <div class="btn-group">
          <div v-if="passwordForm.protocol" class="btn btn-confrim" @click="sendNewpassword">注册</div>
          <div v-else class="btn btn-confrim disable">注册</div>
          <div class="btn btn-cancel" @click="cancelChangePassword">取消</div>
        </div>
    </div>
    <!-- shopo select end -->



  </div>
</template>

<script>
import { Util } from "@/config/util";
import { Public } from "@/config/public";
import { getPhoneCode, loginShop } from "../api";
import { errcode, CustomSex } from "@/config/cfg";

export default {
  data() {
    return {
      passwordForm: {
        name: "",
        sex: 1,
        loginPhone: "",
        code: "",
        messagecode: "",
        newPassword: "",
        newPasswordConfrim: "",
        protocol: false
      },
      sexOptions: [
        {
          value: CustomSex.MAN,
          label: CustomSex.toString(CustomSex.MAN)
        },
        {
          value: CustomSex.FAMEN,
          label: CustomSex.toString(CustomSex.FAMEN)
        },
        {
          value: CustomSex.SECRECY,
          label: CustomSex.toString(CustomSex.SECRECY)
        }
      ],

      pwdType: "password",

      codeimgurl: "",
      checkMsgGray: "获取验证码",
      checkMsg: "获取验证码",
      isShowCheckGrey: false,

      isShowPhoneErr: false,
      isShowRepeatErr: false,
      isShowPhoneEmptyErr: false,
      isShowcodeErr: false,
      isShowPhonecodeEmptyErr: false,
      isShowPhonecodeErr: false,
      isShowPasswordErr: false,
      isShownNameErr: false,
      isShowpasswordEmptyErr: false,

      isShowCapti: false, //大写锁定显示

      base_url: base_url
    };
  },
  mounted() {
    this.init();
    this.initCheckImg();
  },
  created() {
    // document.addEventListener('keydown', (event)=>{
    //   console.log(event);
    // });
  },
  methods: {
    init() {},
    initCheckImg() {
      this.token = Util.creatToken();
      this.codeimgurl =
        base_url +
        "/code.php?height=26&width=100&fontsize=16&codelen=4&token=" +
        this.token +
        "&is_plain=1";
    },
    changeCheckImg() {
      this.codeimgurl =
        base_url +
        "/code.php?height=26&width=100&fontsize=16&codelen=4&token=" +
        this.token +
        "&is_plain=1" +
        "&" +
        Math.random();
    },
    //60s验证码
    setTimerDown() {
      let sec = 60;
      for (let i = 0; i <= 60; i++) {
        setTimeout(() => {
          if (sec !== 0) {
            // debugger;
            this.checkMsgGray = sec + "s后重新获取";
            sec--;
          } else {
            sec = 60; //如果倒计时结束就让  获取验证码显示出来
            this.isShowCheckGrey = true;
            this.checkMsg = "重新获取";
          }
        }, i * 1000);
      }
    },
    //手机号输入校验
    checkPhoneNum() {
      this.passwordForm.loginPhone = Util.checkRound(
        this.passwordForm.loginPhone
      );
      if (this.passwordForm.loginPhone !== "") {
        this.isShowCheckGrey = true;
      }
    },
    //校验手机号
    checkPhoneTrueNum() {
      return Public.checkPhoneNum(this.passwordForm.loginPhone);
    },

    sendPhoneNum() {
      if (this.checkPhoneTrueNum()) {
        let data = {
          get_coke: 1,
          page_code: this.passwordForm.code,
          phone: this.passwordForm.loginPhone,
          name: "registrate"
        };

        getPhoneCode(data, resp => {
          if (resp.ret === 0) {
            this.isShowCheckGrey = false;
            this.setTimerDown();
            this.changeCheckImg();
          } else if (resp.ret === errcode.IMG_ERRL) {
            this.isShowcodeErr = true;
            this.changeCheckImg();
          } else {
            this.changeCheckImg();
            console.warn("获取验证码错误！");
          }
        });
      } else {
        this.isShowPhoneErr = true;
        console.log("请输入正确手机号！");
      }
    },
    cancelChangePassword() {
      this.$router.push("/");
    },
    checkPassword() {
      if (
        this.passwordForm.newPassword !== this.passwordForm.newPasswordConfrim
      ) {
        this.isShowPasswordErr = true;
      } else {
        this.isShowPasswordErr = false;
      }
    },
    clearPasswordtip() {
      this.isShowPasswordErr = false;
    },
    clearCodetip() {
      this.isShowcodeErr = false;
    },
    // 注册
    sendNewpassword() {
      if (this.check()) {
        return;
      }
      if (!this.passwordForm.protocol) {
        return;
      }
      let data = {
        setting_user: 1,
        phone: this.passwordForm.loginPhone,
        sex: this.passwordForm.sex,
        real_name: this.passwordForm.name,
        phone_code: this.passwordForm.messagecode,
        passwd: this.passwordForm.newPassword,
        passwd_again: this.passwordForm.newPasswordConfrim
      };
      // debugger;
      loginShop(data, resp => {
        if (resp.ret === 0) {
          this.$slnotify({
            message: "注册成功"
          });
          setTimeout(() => {
            this.$router.push("/");
          }, 2000);
        } else {
          this.$slnotify({
            message: "注册失败"
          });
          console.warn("注册失败！");
        }
      });
    },
    check() {
      let errBoolean = false;

      if (this.passwordForm.name === "") {
        this.isShownNameErr = true;
        errBoolean = true;
      }

      if (this.passwordForm.loginPhone === "") {
        this.isShowPhoneEmptyErr = true;
        errBoolean = true;
      }

      if (this.passwordForm.messagecode === "") {
        this.isShowPhonecodeEmptyErr = true;
        errBoolean = true;
      }

      if (
        this.passwordForm.newPassword === "" ||
        this.passwordForm.newPasswordConfrim === ""
      ) {
        this.isShowpasswordEmptyErr = true;
        errBoolean = true;
      }

      return errBoolean;
    },
    clearerrTip() {
      this.isShowPhoneErr = false;
      this.isShowRepeatErr = false;
      this.isShowPhoneEmptyErr = false;
      this.isShowcodeErr = false;
      this.isShowPhonecodeEmptyErr = false;
      this.isShowPhonecodeErr = false;
      this.isShowPasswordErr = false;
      this.isShownNameErr = false;
      this.isShowpasswordEmptyErr = false;

      if (this.passwordForm.loginPhone === "") {
        this.isShowCheckGrey = false;
      }
    },
    opentoggleCase() {
      this.isShowCapt = true;
    },
    closetoggleCase() {
      this.isShowCapt = false;
    },
    detectCapsLock(event) {
      // debugger;
      let e = event || window.event;
      let keyCode = e.keyCode || e.which;
      let isShift = e.shiftKey || keyCode === 16 || false;
      if (
        (keyCode >= 65 && keyCode <= 90 && !isShift) ||
        (keyCode >= 97 && keyCode <= 122 && isShift)
      ) {
        this.isShowCapti = true;
      } else {
        this.isShowCapti = false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#forget-password {
  width: 100%;
  height: 100vh;
  background-image: url(./images/loadingpage.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  .logo {
    position: absolute;
    @include wh(212px, 72px);
    @include tl(50px, 60px);

    img {
      @include wh(212px, 72px);
    }
  }
  .select-content {
    height: 80%;
    background-color: #fff;
    position: absolute;
    left: 40px;
    right: 40px;
    top: 16%;
    box-shadow: 0 2px 4px 0 #becaeb;
    border-radius: 2px;

    .label {
      position: absolute;
      @include wh(230px, 50px);
      @include fc(20px, #fff);
      @include tl(50px,-40px);
      background-color: #4877e7;
      border-radius: 0 25px 25px 0;
      font-weight: bold;
      line-height: 50px;
      text-align: right;
      padding-right: 20px;
    }
    .from-content {
      margin-top: 150px;
      margin-left: 87px;
    }
  }
}
.password-form {
  width: 545px;
}
.check-code {
  position: relative;

  .check-img {
    border-radius: 2px;
    position: absolute;
    top: 4px;
    right: 182px;
    cursor: pointer;
  }
}

.check-msg {
  position: relative;

  .check-message {
    @include wh(100px, 24px);
    text-align: center;
    line-height: 24px;
    border-radius: 2px;
    position: absolute;
    top: 5px;
    right: 182px;
    cursor: pointer;
    background: #4877e7;
    color: #fff;
    font-size: 12px;
  }
  .gray {
    background: #d8d8d8;
  }
}

.errMessage {
  @include fc(12px, #e7487e);
  margin-left: 40px;
}
.btn-group {
  position: absolute;
  left: 50%;
  bottom: 50px;
  transform: translateX(-50%);
  .btn {
    @include wh(160px,40px);
    line-height: 40px;
    text-align: center;
    border-radius: 2px;
    font-size: 14px;
    color: #4877e7;
    display: inline-block;
    border: 1px solid #4877e7;
    cursor: pointer;
  }
  .btn-confrim {
    background: #4877e7;
    color: #fff;
    margin-right: 60px;
  }
  .disable {
    background-color: #c4c4c4;
    cursor: no-drop;
    border: 1px solid #c4c4c4;
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#forget-password {
  .el-input .el-input__inner {
    width: 280px;
    height: 34px;
    border-radius: 0;
  }
  .el-form-item__content {
    line-height: 34px;
  }
  .el-form-item__label {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .el-input.errtip .el-input__inner {
    border-color: #e7487e;
  }
  .el-form-item {
    display: inline-block;
  }
}
</style>






